.resume-page {
    min-height: 75vh;
    margin-block-start: 120px;
    > iframe {
        width: 850px;
        height: 1070px;
        resize: vertical;
        margin: auto;
        margin-block-end: 5rem;

        @media (max-width: $normal-breakpoint) {
            width: 100%;
            height: 105vw;
        }
    }
}
